@media (prefers-reduced-motion: no-preference) {
  * {
    scroll-behavior: smooth;
  }
}

@media only screen and (max-width: 400px) {
  header {
    height: 100px !important;
  }

  main {
    padding-top: 100px !important;
    /* header 50px height*/
  }

  .outlinediv {
    max-width: 350px;
  }
}

/* h2 is About */
h2 {
  text-align: left;
  font-family: Verdana, Tahoma;
  letter-spacing: 1.5px;
  border-bottom: 4px solid #dfdfe2;
  max-width: 80%;
  margin-top: 0px;
  padding-top: 10px;
}

h2::before {
  content: "//";
}

.info {
  padding: 0.5px;
  width: 80%;
  text-align: justify;
  padding: 0% 5% 0% 5%;
  margin: 0% auto 10px auto;
}

/* About */

main {
  padding-top: 50px;
  /* header 50px height*/
}

body {
  text-align: center;
  align-items: center;
  background: #1b1b32;
  color: #efefef;
  font-family: Helvetica;
  margin: 0;
}

body a {
  color: #efefef;
  text-decoration: none !important;
}

body a:hover {
  text-decoration: underline;
  background-color: #f1be32;
  color: #000000;
}


.outerdiv {
  width: 85%;
  text-align: justify;
  padding: 2%;
  margin-left: auto;
  margin-right: auto;
  color: #1b1b32;
  border-radius: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

@media (min-width: 800px) {

  .input,
  .timer {
    width: 45%;
  }
}

@media (max-width: 799px) {

  .input,
  .timer {
    width: 80%;
  }
}

@media (max-width: 400px) {
  .inputpanel {
    font-size: large;
  }
}

.input {
  max-width: 400px;
  width: 100%;
  aspect-ratio: 2/1;
  text-align: justify;
  padding: 3%;
  background-color: #aaaaaa;
  color: #1b1b32;
  border-radius: 15px;

  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  gap: 2%;
}

.inputpanel {
  font-family: Verdana, Tahoma;
  font-weight: bold;

  text-align: center;
  padding: 3%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  container-type: size;
}

#breakinputdiv,
#sessioninputdiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
}

.length-control {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-content: center;
  align-items: center;
}

.lengthdiv {
  margin: 10% auto 10% auto;
  display: flex;
  height: 25%;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: stretch;
  width: 100%;
  gap: 2%;
}

#break-length,
#session-length {
  font-size: x-large;
}

.button-panel {
  display: flex;
  justify-content: space-around;
  width: 100%;
  flex-flow: row;
}

.outlinediv {
  max-width: 1050px;
  width: 85%;
  text-align: justify;
  padding: 2% 0% 2% 0%;
  margin-left: auto;
  margin-right: auto;
  background-color: #888888;
  color: #000000;
  border-radius: 15px;

  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  gap: 10px;
}

.opspad {
  width: 100%;
  text-align: justify;
  padding: 0%;
  /*   background-color: #f1be32;*/
  color: #000000;
  border-radius: 15px;
  /* flex align*/
  justify-content: center;
  align-items: center;
  align-content: stretch;
  /* Use CSS Grid to create a 2x2 grid */
  display: grid;
  grid-template-columns: repeat(2, 25%);
  gap: 5px;
}

.opspad .opskey {
  cursor: pointer;
  font-family: Verdana, Tahoma;
  font-weight: bold;
  /* Use flexbox to center content and make {id} center */
  display: flex;
  align-items: center;
  justify-content: center;

  /* https://stackoverflow.com/questions/30693928/how-to-make-font-size-relative-to-parent-div */
  container-type: size;

  border: 5px outset #000000;
  border-radius: 15px;
  aspect-ratio: 2/1;
}

.lengthkey {
  cursor: pointer;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;

  background-color: #888888;
  color: #1b1b32;
  border: 3px outset #000000;
  border-radius: 10px;
  aspect-ratio: 1/1;
  margin: 2% 20% 2% 20%;

  display: flex;
  justify-content: center;
  align-items: center;
}

#start_stop {
  background-color: #880000;
  color: #dfdfe2;
}

#reset {
  background-color: #888888;
  color: #1b1b32;
}

#reset:hover {
  background-image: linear-gradient(rgba(151, 255, 151, 0.4) 0 0);
}

.opspad .opskey:hover,
.lengthkey:hover {
  background-image: linear-gradient(rgba(192, 192, 192, 0.4) 0 0);
}

.opspad .opskey:active,
.lengthkey:active {
  background-image: linear-gradient(rgba(192, 192, 192, 0.6) 0 0);
  transform: translate(4px, 4px);
}

.opspad .opskey span {
  resize: both;
  font-size: 15px;
  /*80cqmin;*/
  font-weight: bold;
}

.lengthkey span {
  resize: both;
  font-size: 25px;
  /*80cqmin;*/
  font-weight: bold;
}

.display {
  width: 45%;
  height: 25%;
  margin: auto;
  padding: 2%;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  display: flex;
}

#time-left {
  /*result display*/
  color: #1b1b32;
  background-color: #9bbc0f;
  border: 10px outset #000000;
  border-radius: 10px;
  font-size: 60px;
  /*8cqmin;*/
  overflow: hidden;
  resize: none;
  /*although it said left but left is only the question required name...*/
  justify-content: center;
  align-content: center;
  align-items: center;
}

.timer {
  max-width: 400px;
  width: 100%;
  aspect-ratio: 2/1;
  text-align: justify;
  padding: 3%;
  background-color: #aaaaaa;
  color: #1b1b32;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  position: relative;
}

#timer-label {
  font-family: Verdana, Tahoma;
  font-weight: bold;
  font-size: 220%;
  height: 25%;
  text-align: center;
  padding: 3%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  container-type: size;
}

#tomato {
  font-family: Verdana, Tahoma;
  font-weight: bold;
  font-size: 600%;
  height: 25%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /*  Fix the tomato display behind others*/
  position: absolute;
  bottom: 0;
  left: -5%;
}